html {
    font-family: "Roboto", "Noto Sans TC", "微軟正黑體", "Microsoft JhengHei", "Arial Unicode MS", sans-serif;
    height: 100%;
    &.fixed {
        overflow: hidden;
    }
}

body {
    background-color: var(--color-main-500);
    height: 100%;
    &.fixed {
        overflow: hidden;
    }
}

.site-main {
    position: relative;
    &::after {
        content: "";
        height: 117.2rem;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        background: transparent url('../images/overlay.jpg') no-repeat center 0;
        background-size: auto 100%;
        opacity: 0.5;
        pointer-events: none;
        display: none;
    }
}

.wrap {
    position: relative;
    overflow: hidden;
    &::before {
        content: "";
        height: 117vw;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        background: transparent url('../images/home-glow-bg.jpg') no-repeat center 0;
        background-size: cover;
        @include susy-breakpoint($s-320, $c8) {
            background-image: url('../images/home-glow-bg-m.jpg');
            height: 205vw;
        }
    }
    &.success {
        &::before {
            display: none;
        }
    }
}

.section-title {
    display: block;
    margin: 0 auto 5.6rem;
    text-align: center;
    font-size: 0;
    @include susy-breakpoint($s-320, $c8) {
        margin-bottom: 2.4rem;
    }
    @include susy-breakpoint($s-768, $c8) {
        margin-bottom: 3.2rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        margin-bottom: 4rem;
    }
    &__text {
        background: rgb(231,136,0);
        background: linear-gradient(1deg, rgba(231,136,0,1) 20%, rgba(247,232,174,1) 68%, rgba(218,156,69,1) 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        display: inline-block;
        font-size: 3.6rem;
        vertical-align: middle;
        padding: 0 2.4rem;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 2.4rem;
            padding-left: 1.2rem;
            padding-right: 1.2rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            font-size: 2.4rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 2.4rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            font-size: 3.2rem;
        }
    }
    &__pattern {
        vertical-align: middle;
        display: inline-block;
        width: calc(100% - 92.5rem);
        height: 1.6rem;
        background: transparent url('../images/title-pattern.png') repeat-x 0 top;
        background-size: 8.2rem 1.6rem;
        @include susy-breakpoint($s-320, $c8) {
            width: 7.4rem;
            height: 1.6rem * 0.7;
            background-size: 8.2rem*0.7 1.6rem*0.7;
        }
        @include susy-breakpoint($s-768, $c8) {
            width: calc(100% - 46rem);
            height: 1.6rem * 0.85;
            background-size: 8.2rem*0.85 1.6rem*0.85;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: calc(100% - 54.5rem);
            height: 1.6rem * 0.85;
            background-size: 8.2rem*0.85 1.6rem*0.85;
        }
        @include susy-breakpoint($s-1201, $c18) {
            width: calc(100% - 66.5rem);
            height: 1.6rem * 0.85;
            background-size: 8.2rem*0.85 1.6rem*0.85;
        }
    }
}

.section-sub-title {
    width: 21.7rem;
    height: 4.7rem;
    background: transparent url('../images/sub-title-bg@3x.png') no-repeat 0 0;
    background-size: cover;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto 4rem;
    @include susy-breakpoint($s-320, $c8) {
        margin-bottom: 1.6rem;
        width: 21.7rem * 0.6;
        height: 4.7rem * 0.6;
    }
    @include susy-breakpoint($s-768, $c8) {
        margin-bottom: 2.4rem;
        width: 21.7rem * 0.8;
        height: 4.7rem * 0.8;
    }
    @include susy-breakpoint($s-960, $c12) {
        margin-bottom: 2.4rem;
        width: 21.7rem * 0.8;
        height: 4.7rem * 0.8;
    }
    @include susy-breakpoint($s-1201, $c18) {
        margin-bottom: 3.2rem;
    }
    span {
        font-size: 2.4rem;
        background: rgb(231,136,0);
        background: linear-gradient(1deg, rgba(231,136,0,1) 20%, rgba(247,232,174,1) 68%, rgba(218,156,69,1) 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        font-weight: 500;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.6rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            font-size: 1.8rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 2rem;
        }
    }
}

.section-text {
    @extend %site-paragraph;
}

.btn-gotop {
    background: transparent url('../images/btn-top@3x.png') no-repeat 0 0;
    background-size: cover;
    width: 4.8rem;
    height: 4.8rem;
    position: fixed;
    right: 0;
    bottom: 0;
    z-index: 100;
    padding: 0;
    border: 0;
    transition: all 300ms ease-in-out;
    transform: translate3d(-50%,100%,0);
    &.is-active {
        transform: translate3d(-50%,-50%,0);
    }
    @include susy-breakpoint($s-320, $c8) {
        width: 3.2rem;
        height: 3.2rem;
    }
}