.section-nav {
    &__nav {
        width: 100%;
        margin: 0 auto;
        background: rgb(0,0,138);
        background: linear-gradient(0deg, rgba(0,0,138,1) 0%, rgba(24,62,217,1) 74%);
        border: solid 0.2rem var(--color-yellow-200-g);
        padding: 3.2rem 0 4rem;
        display: flex;
        justify-content: space-around;
        border-radius: 3rem;
        @include susy-breakpoint($s-320, $c8) {
            border-radius: 1.6rem;
            padding-top: 2rem;
            padding-bottom: 2.8rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            border-radius: 1.6rem;
            padding-top: 2.8rem;
            padding-bottom: 3.6rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            border-radius: 2rem;
            padding-top: 2.8rem;
            padding-bottom: 3.6rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            border-radius: 2.4rem;
        }
    }
    &__btn {
        display: block;
        position: relative;
        &::after {
            content: "";
            width: 1.9rem;
            height: 1.1rem;
            position: absolute;
            left: 50%;
            bottom: 0;
            background: transparent url('../images/icon-arrow-down@3x.png') no-repeat 0 0;
            background-size: 1.9rem 1.1rem;
            transform: translate3d(-50%,180%,0);
            @include susy-breakpoint($s-320, $c8) {
                width: 1.9rem*0.6;
                height: 1.1rem*0.6;
                background-size: 1.9rem*0.6 1.1rem*0.6;
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 1.9rem*0.7;
                height: 1.1rem*0.7;
                background-size: 1.9rem*0.7 1.1rem*0.7;
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 1.9rem*0.8;
                height: 1.1rem*0.8;
                background-size: 1.9rem*0.8 1.1rem*0.8;
            }
        }
        span {
            font-size: 3rem;
            background: rgb(247,232,174);
            background: radial-gradient(circle, rgba(247,232,174,1) 0%, rgba(225,181,106,1) 100%);
            font-weight: 900;
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.8rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                font-size: 1.8rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 2rem;
            }
            @include susy-breakpoint($s-1201, $c18) {
                font-size: 2.4rem;
            }
        }
    }
}