.section-home {
    width: 100%;
    height: 58.6vw;
    position: relative;
    margin-bottom: 6.6rem;
    @include susy-breakpoint($s-320, $c8) {
        height: 135vw;
        margin-bottom: 4vw;
    }
    &__joystick {
        width: 44.5vw;
        position: absolute;
        left: 50%;
        top: 0;
        transform: translate3d(-50%,-16%,0);
        z-index: 10;
        img {
            @extend %rsp-img;
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 85vw;
            transform: translate3d(-50%,-19%,0);
        }
    }
    &__card-1500,
    &__card-2000,
    &__card-1188,
    &__card-1000 {
        position: absolute;
        width: 9.4vw;
        top: 0;
        left: 50%;
        img {
            @extend %rsp-img;
        }
        @include susy-breakpoint($s-320, $c8) {
            display: none;
        }
    }
    &__card-1500 {
        transform: translate3d(-478%,-24%,0);
    }
    &__card-2000 {
        transform: translate3d(-343%,-48%,0);
    }
    &__card-1188 {
        transform: translate3d(254%,-51%,0);
    }
    &__card-1000 {
        transform: translate3d(380%,-38%,0);
    }
    &__prize-list {
        z-index: 200;
        background: rgb(0,20,124);
        background: linear-gradient(180deg, rgba(0,20,124,1) 0%, rgba(0,20,124,0) 70%);
        position: absolute;
        width: 100%;
        height: 12vw;
        top: 44vw;
        left: 0;
        transform: translate3d(0,0,0);
        @include susy-breakpoint($s-320, $c8) {
            top: 90vw;
            background-image: none;
        }
    }
    &__prize-1,
    &__prize-2,
    &__prize-3,
    &__prize-4,
    &__prize-5,
    &__prize-6 {
        position: absolute;
        left: 50%;
        top: 0;
        img {
            @extend %rsp-img;
        }
    }
    &__prize-1 {
        width: 5.5vw;
        transform: translate3d(627%,-56%,0);
        @include susy-breakpoint($s-320, $c8) {
            width: 8vw;
            transform: translate3d(484%,-99%,0);
        }
    }
    &__prize-2 {
        width: 21.5vw;
        transform: translate3d(82%,-36%,0);
        @include susy-breakpoint($s-320, $c8) {
            width: 37.5vw;
            transform: translate3d(41%,-27%,0);
        }
    }
    &__prize-3 {
        width: 18.2vw;
        transform: translate3d(-240%,-29%,0);
        @include susy-breakpoint($s-320, $c8) {
            width: 28.2vw;
            transform: translate3d(-170%,-12%,0);
        }
    }
    &__prize-4 {
        width: 21.6vw;
        transform: translate3d(-137%,-59%,0);
        @include susy-breakpoint($s-320, $c8) {
            width: 30.6vw;
            transform: translate3d(-120%,-86%,0);
        }
    }
    &__prize-5 {
        width: 21.5vw;
        transform: translate3d(24%,-67%,0);
        @include susy-breakpoint($s-320, $c8) {
            width: 30vw;
            transform: translate3d(24%,-91%,0);
        }
    }
    &__prize-6 {
        width: 30vw;
        transform: translate3d(-50%,-42%,0);
        @include susy-breakpoint($s-320, $c8) {
            width: 46vw;
            transform: translate3d(-44%,-28%,0);
        }
    }
    &__sub-title {
        z-index: 250;
        width: 16.7vw;
        position: absolute;
        left: 50%;
        top: 27vw;
        transform: translate3d(-54%,0,0);
        img {
            @extend %rsp-img;
            &.move {
                animation: move 1.5s infinite ease-in-out;
            }
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 40vw;
            top: 49vw;
        }
    }
    &__symbol {
        position: absolute;
        left: 50%;
        top: 1vw;
        transform: translate3d(-53%,0,0);
        z-index: 0;
        width: 92vw;
        img {
            @extend %rsp-img;
        }
        @include susy-breakpoint($s-320, $c8) {
            top: 4vw;
            transform: translate3d(-50%,0,0);
        }
    }
    &__text {
        background: rgb(225,181,106);
        background: linear-gradient(0deg, rgba(225,181,106,1) 0%, rgba(247,232,174,1) 57%, rgba(225,183,108,1) 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        font-size: 1.5vw;
        font-family: din-2014, 'Noto Sans TC';
        text-align: center;
        position: absolute;
        width: 100%;
        bottom: 0;
        font-weight: 500;
        @include susy-breakpoint($s-320, $c8) {
            padding: 0 2rem;
            font-size: 4vw;
            line-height: 1.6;
            background: linear-gradient(0deg, rgba(225,181,106,1) 0%, rgba(247,232,174,1) 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
        }
    }
    &__btn-winner {
        background-color: rgba(#00163f,0.95);
        display: flex;
        align-items: center;
        color: #fcfac1;
        position: absolute;
        border: solid 0.1rem #e4f6ff;
        right: 2rem;
        top: 2rem;
        gap: 1rem;
        padding: 1.6rem 2.4rem;
        border-radius: 6rem;
        z-index: 300;
        animation: move 1.5s infinite ease-in-out;
        @include susy-breakpoint($s-320, $c8) {
            padding: 0.8rem 1.6rem;
            gap: 0.6rem;
            right: 1.2rem;
            top: 1.2rem;
        }
        span {
            font-size: 2.4rem;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.6rem;
            }
        }
        img {
            width: 2.4rem;
            display: block;
            margin: 0;
            @include susy-breakpoint($s-320, $c8) {
                width: 1.6rem;
            }
        }
    }
}

@keyframes move {
	0% {
		transform: translate3d(0,0,0);
	}
	50% {
		transform: translate3d(0,-5%,0);
	}
	100% {
		transform: translate3d(0,0,0);
	}
}