.preload {
	width: 100%;
	height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
	background-color: #001a9e;
	position: fixed;
	top: 0;
	left: 0;
	z-index: 999;
	transition: opacity 2s ease;
	&.loaded {
		opacity: 0;
		pointer-events: none;
	}
	.cls-1{
		fill:none;
	}
	.cls-2{
		fill:#ceb173;
	}
	&__lds-ellipsis {
		opacity: 0;
		width: 80px;
		height: 80px;
		position: relative;
		transform: rotate(45deg);
		& > div {
			position: absolute;
			top: 0;
			width: 40px;
			height: 40px;
			animation-timing-function: cubic-bezier(.96,-0.01,.01,1);
			svg {
				width: 24px;
				transform: rotate(-45deg);
			}
			&:nth-child(1) {
				left: 0;
				animation: lds-ellipsis1 3s infinite;
			}
			&:nth-child(2) {
				left: 0;
				animation: lds-ellipsis2 3s infinite;
			}
			&:nth-child(3) {
				left: 0;
				animation: lds-ellipsis3 3s infinite;
			}
			&:nth-child(4) {
				left: 0;
				animation: lds-ellipsis4 3s infinite;
			}
		}
	}
}

@keyframes lds-ellipsis1 {
	0% {
		transform: translate3d(0,0,0);
	}
	25% {
		transform: translate3d(40px,0,0);
	}
	50% {
		transform: translate3d(40px,40px,0);
	}
	75% {
		transform: translate3d(0,40px,0);
	}
	100% {
		transform: translate3d(0,0,0);
	}
}
