%rsp-img {
    max-width: 100%;
    display: block;
    margin: 0 auto;
    height: auto;
}

%site-paragraph {
    font-weight: 500;
    font-size: 2rem;
    line-height: 1.6;
    color: var(--color-yellow-50);
    a {
        color: var(--color-yellow-50);
        border-bottom: 0.1rem solid var(--color-yellow-50);;
    }
    &.sm {
        font-size: 1.6rem;
    }
    @include susy-breakpoint($s-320, $c8) {
        font-size: 1.4rem;
        &.sm {
            font-size: 1.2rem;
        }
    }
    @include susy-breakpoint($s-768, $c8) {
        font-size: 1.4rem;
        &.sm {
            font-size: 1.2rem;
        }
    }
    @include susy-breakpoint($s-960, $c12) {
        font-size: 1.4rem;
        &.sm {
            font-size: 1.2rem;
        }
    }
    @include susy-breakpoint($s-1201, $c18) {
        font-size: 1.6rem;
        &.sm {
            font-size: 1.4rem;
        }
    }
}