.popup-ui {
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    opacity: 0;
    pointer-events: none;
    z-index: 300;
    transition: all 300ms ease;
    &.active {
        pointer-events: auto;
        opacity: 1;
    }
    &__overlay {
        background-color: rgba(0,0,0,0.7);
        display: flex;
        height: 100%;
        width: 100%;
        justify-content: center;
        align-items: center;
        &.scroll {
            overflow-y: scroll;
            align-items: flex-start;
        }
    }
    &__inner {
        width: 50rem;
        position: relative;
        &.winner {
            padding-top: 14rem;
            padding-bottom: 8rem;
        }
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
        }
    }
    &__btn-close {
        width: 5.6rem;
        height: 5.6rem;
        position: absolute;
        left: 50%;
        top: -8rem;
        background: transparent url('../images/popup-ui-btn-close@2x.png') no-repeat 0 0;
        background-size: cover;
        border: 0;
        transition: all 300ms ease;
        transform: translate3d(-50%,0,0);
        font-size: 0;
        @include susy-breakpoint($s-320, $c8) {
            width: 4.8rem;
            height: 4.8rem;
            top: -6rem;
        }
        &.winner {
            top: 4rem;
        }
    }
    &__content {
        box-sizing: border-box;
        width: 50rem;
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            padding: 0 2rem;
        }
    }
    &__img {
        width: auto;
        display: block;
        margin: 0 auto;
        height: 36vw;
        @include susy-breakpoint($s-320, $c8) {
            height: 100vw;
        }
        @include susy-breakpoint($s-768, $c8) {
            height: 60vw;
        }
    }
    &__invoice-img {
        width: auto;
        display: block;
        margin: 0 auto;
        height: 36vw;
        @include susy-breakpoint($s-320, $c8) {
            height: 100vw;
        }
        @include susy-breakpoint($s-768, $c8) {
            height: 60vw;
        }
    }
    &__winner-block {
        h2 {
            border: solid 0.1rem #d0c897;
            display: flex;
            background-color: #1433cb;
            height: 5.8rem;
            font-size: 2.4rem;
            color: #d0c897;
            align-items: center;
            justify-content: center;
            font-weight: 500;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 2rem;
                height: 4.8rem;
            }
        }
    }
    &__step {
        &:not(:last-child) {
            margin-bottom: 4rem;
        }
        & > h3 {
            display: flex;
            background-color: var(--color-yellow-400-g);
            font-size: 2.4rem;
            color: #222;
            height: 5.8rem;
            align-items: center;
            justify-content: center;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 2rem;
                height: 4.8rem;
            }
        }
    }
    &__winner-row {
        display: flex;
        &:not(:last-child) {
            border-bottom: 0.1rem solid #c8c8c8;
        }
        div {
            background-color: #fff;
            display: flex;
            height: 5.6rem;
            font-size: 1.8rem;
            color: #434343;
            flex: 1;
            justify-content: center;
            align-items: center;
            &:nth-child(1) {
                border-right: solid 0.1rem #c8c8c8;
            }
        }
    }
}