.section-event {
    $self: &;
    padding-top: 8.8rem;
    padding-bottom: 12rem;
    @include susy-breakpoint($s-320, $c8) {
        padding-top: 4rem;
        padding-bottom: 5.6rem;
    }
    @include susy-breakpoint($s-768, $c8) {
        padding-top: 6.4rem;
        padding-bottom: 8rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        padding-top: 6.4rem;
        padding-bottom: 8rem;
    }
    &__block {
        &:not(:last-child) {
            margin-bottom: 7.2rem;
            @include susy-breakpoint($s-320, $c8) {
                margin-bottom: 2.4rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                margin-bottom: 2.4rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                margin-bottom: 3.6rem;
            }
            @include susy-breakpoint($s-1201, $c18) {
                margin-bottom: 3.6rem;
            }
        }
    }
    &__content {
        &:not(:last-child) {
            margin-bottom: 2.4rem;
            @include susy-breakpoint($s-320, $c8) {
                margin-bottom: 1.2rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                margin-bottom: 1.2rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                margin-bottom: 1.6rem;
            }
            @include susy-breakpoint($s-1201, $c18) {
                margin-bottom: 1.6rem;
            }
        }
        h4 {
            @extend %site-paragraph;
        }
    }
    &__ul-remind-list {
        li {
            @extend %site-paragraph;
            padding-left: 2.4rem;
            position: relative;
            @include susy-breakpoint($s-320, $c8) {
                padding-left: 1.2rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                padding-left: 1.6rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                padding-left: 1.6rem;
            }
            &:not(:last-child) {
                margin-bottom: 0.8rem;
                @include susy-breakpoint($s-320, $c8) {
                    margin-bottom: 0.4rem;
                }
                @include susy-breakpoint($s-768, $c8) {
                    margin-bottom: 0.4rem;
                }
                @include susy-breakpoint($s-960, $c12) {
                    margin-bottom: 0.4rem;
                }
            }
            &::before {
                content: "";
                border-radius: 50%;
                background-color: var(--color-yellow-50);
                left: 0.6rem;
                top: 1.6rem;
                position: absolute;
                width: 0.6rem;
                height: 0.6rem;
                @include susy-breakpoint($s-320, $c8) {
                    width: 0.4rem;
                    height: 0.4rem;
                    left: 0.3rem;
                    top: 0.7rem;
                }
                @include susy-breakpoint($s-768, $c8) {
                    width: 0.4rem;
                    height: 0.4rem;
                    left: 0.6rem;
                    top: 0.9rem;
                }
                @include susy-breakpoint($s-960, $c12) {
                    width: 0.4rem;
                    height: 0.4rem;
                    left: 0.6rem;
                    top: 0.9rem;
                }
            }
        }
    }
    &__ol-list {
        list-style: decimal;
        padding-left: 2.4rem;
        li {
            @extend %site-paragraph;
            &:not(:last-child) {
                margin-bottom: 0.8rem;
            }
        }
    }
    &__ol-rule-list {
        list-style: decimal;
        padding-left: 2.4rem;
        li {
            @extend %site-paragraph;
            font-size: 1.6rem;
            &:not(:last-child) {
                margin-bottom: 0.8rem;
            }
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.4rem;
                :not(:last-child) {
                    margin-bottom: 0.4rem;
                }
            }
            @include susy-breakpoint($s-768, $c8) {
                font-size: 1.4rem;
                :not(:last-child) {
                    margin-bottom: 0.4rem;
                }
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 1.4rem;
                :not(:last-child) {
                    margin-bottom: 0.4rem;
                }
            }
        }
    }
    &__prize {
        position: relative;
        overflow: hidden;
        &::before {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            background: rgb(0,20,124);
            background: linear-gradient(180deg, rgba(0,20,124,1) 0%, rgba(0,20,124,0) 20%);
            left: 0;
            top: 66%;
            z-index: 0;
            transform: translate3d(0,0,0);
            @include susy-breakpoint($s-320, $c8) {
                display: none;
            }
            @include susy-breakpoint($s-768, $c8) {
                top: 50%;
            }
        }
        @include susy-breakpoint($s-320, $c8) {
            .container {
                padding-left: 0;
                padding-right: 0;
            }
        }
    }
    &__group {
        display: flex;
        @include susy-breakpoint($s-320, $c8) {
            position: relative;
            overflow: hidden;
            padding-left: 2rem;
            padding-right: 2rem;
            &::before {
                content: "";
                left: 0;
                top: 66%;
                height: 100%;
                position: absolute;
                width: 100%;
                background: rgb(0,20,124);
                background: linear-gradient(180deg, rgba(0,20,124,1) 0%, rgba(0,20,124,0) 20%);
                z-index: 0;
            }
            &:nth-child(1) {
                order: 3;
                #{$self}__prize-item {
                    &:nth-child(1) {
                        order: 2;
                    }
                    &:nth-child(2) {
                        order: 1;
                    }
                }
            }
            &:nth-child(2) {
                order: 2;
            }
            &:nth-child(3) {
                order: 1;
            }
        }
    }
    &__prize-wrap {
        position: relative;
        z-index: 10;
        display: flex;
        @include susy-breakpoint($s-320, $c8) {
            display: grid;
            grid-template-columns: mixmax(0, 1fr);
            gap: 2.4rem;
        }
    }
    &__prize-item {
        @include susy-breakpoint($s-320, $c8) {
            position: relative;
            z-index: 10;
        }
        img {
            @extend %rsp-img;
        }
        figcaption {
            padding-top: 1.2rem;
            font-size: 1.8rem;
            color: var(--color-yellow-50);
            line-height: 1.4;
            text-align: center;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.2rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                font-size: 1.4rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 1.4rem;
            }
            @include susy-breakpoint($s-1201, $c18) {
                font-size: 1.6rem;
            }
        }
    }
}