.section-signup {
    $self: &;
    &__row {
        &:not(:last-child) {
            margin-bottom: 5.6rem;
            @include susy-breakpoint($s-320, $c8) {
                margin-bottom: 2rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                margin-bottom: 2.4rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                margin-bottom: 3.2rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                margin-bottom: 4rem;
            }
        }
        &.half {
            display: grid;
            grid-template-columns: repeat(2, minmax(0, 1fr));
            gap: 8rem;
            @include susy-breakpoint($s-320, $c8) {
                gap: 2.4rem;
                grid-template-columns: minmax(0, 1fr);
            }
            @include susy-breakpoint($s-768, $c8) {
                gap: 2rem;
                grid-template-columns: minmax(0, 1fr);
            }
            @include susy-breakpoint($s-960, $c12) {
                gap: 2.4rem;
                grid-template-columns: minmax(0, 1fr);
            }
            @include susy-breakpoint($s-1201, $c18) {
                gap: 4rem;
            }
        }
    }
    &__field {
        position: relative;
        &.alert {
            #{$self}__alert-message {
                opacity: 1;
            }
        }
    }
    &__invoice {
        margin-bottom: 8rem;
        @include susy-breakpoint($s-320, $c8) {
            margin-bottom: 2.4rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            margin-bottom: 3.2rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            margin-bottom: 4.8rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            margin-bottom: 6.4rem;
        }
    }
    &__survey {
        background-color: var(--color-main-600);
        padding: 8rem 0;
        @include susy-breakpoint($s-320, $c8) {
            padding-top: 2.4rem;
            padding-bottom: 2.4rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            padding-top: 3.2rem;
            padding-bottom: 3.2rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            padding-top: 4.8rem;
            padding-bottom: 4.8rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            padding-top: 6.4rem;
            padding-bottom: 6.4rem;
        }
    }
    &__label {
        display: flex;
        align-items: center;
        font-size: 2rem;
        color: var(--color-yellow-50);
        margin-bottom: 2rem;
        padding-left: 4.4rem;
        line-height: 1.6;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.2rem;
            padding-left: 1.2rem;
            margin-bottom: 0.8rem;
            flex-wrap: wrap;
        }
        @include susy-breakpoint($s-768, $c8) {
            padding-left: 1.6rem;
            margin-bottom: 1.2rem;
            font-size: 1.4rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 1.4rem;
            padding-left: 1.6rem;
            margin-bottom: 1.2rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            font-size: 1.6rem;
            padding-left: 2.4rem;
            margin-bottom: 1.6rem;
        }
    }
    &__alert-message {
        font-size: 2rem;
        color: var(--color-orange-500);
        padding-left: 0.8rem;
        opacity: 0;
        transition: all 300ms ease;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 1.2rem;
            padding-left: 0.4rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            font-size: 1.4rem;
            padding-left: 0.4rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 1.4rem;
            padding-left: 0.4rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            font-size: 1.6rem;
        }
    }
    &__input {
        box-sizing: border-box;
        height: 9.6rem;
        width: 100%;
        border-radius: 3rem;
        border: solid 0.2rem var(--color-yellow-100);
        font-size: 2rem;
        padding: 0 4.4rem;
        color: var(--color-yellow-100);
        background-color: transparent;
        transition: all 300ms ease;
        &::placeholder {
            color: var(--color-yellow-100);
        }
        &:focus {
            border-color: var(--color-orange-500);
        }
        @include susy-breakpoint($s-320, $c8) {
            padding-left: 1.2rem;
            padding-right: 1.2rem;
            height: 4.8rem;
            border-radius: 1.2rem;
            font-size: 1.2rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            padding-left: 1.6rem;
            padding-right: 1.6rem;
            height: 5.6rem;
            border-radius: 1.6rem;
            font-size: 1.4rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            padding-left: 1.6rem;
            padding-right: 1.6rem;
            height: 6.4rem;
            border-radius: 2rem;
            font-size: 1.4rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            padding-left: 2.4rem;
            padding-right: 2.4rem;
            height: 8rem;
            border-radius: 2.4rem;
            font-size: 1.6rem;
        }
    }
    &__inner-row {
        display: grid;
        grid-template-columns: repeat(2, minmax(0, 1fr));
        gap: 4rem;
        &.full {
            grid-template-columns: 1fr;
        }
        &.adddres {
            grid-template-columns: repeat(2, minmax(0, 1fr)) 2fr;
            @include susy-breakpoint($s-320, $c8) {
                grid-template-columns: repeat(2, minmax(0, 1fr));
                grid-template-rows: repeat(2, minmax(0, 1fr));
                #{$self}__select-wrap {
                    &:nth-child(1) {
                        grid-column: 1 / 2;
                        grid-row: 1 / 2;
                    }
                    &:nth-child(2) {
                        grid-column: 2 / 3;
                        grid-row: 1 / 2;
                    }
                }
                #{$self}__input {
                    grid-column: 1 / 3;
                    grid-row: 2 / 3;
                }
            }
        }
        @include susy-breakpoint($s-320, $c8) {
            gap: 1.2rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            gap: 1.2rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            gap: 1.6rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            gap: 2rem;
        }
    }
    &__select-wrap {
        box-sizing: border-box;
        width: 100%;
        height: 9.6rem;
        position: relative;
        border-radius: 3rem;
        overflow: hidden;
        @include susy-breakpoint($s-320, $c8) {
            height: 4.8rem;
            border-radius: 1.2rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            height: 5.6rem;
            border-radius: 1.6rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            height: 6.4rem;
            border-radius: 2rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            height: 8rem;
            border-radius: 2.4rem;
        }
        &::before {
            width: 2.2rem;
            height: 1.3rem;
            pointer-events: none;
            z-index: 10;
            content: "";
            position: absolute;
            right: 3rem;
            top: 50%;
            transform: translate3d(0,-50%,0);
            background: transparent url('../images/icon-arrow-down-2@3x.png') no-repeat 0 0;
            background-size: 2.2rem 1.3rem;
            @include susy-breakpoint($s-320, $c8) {
                width: 2.2rem * 0.6;
                height: 1.3rem * 0.6;
                background-size: 2.2rem*0.6 1.3rem*0.6;
                right: 1.6rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 2.2rem * 0.6;
                height: 1.3rem * 0.6;
                background-size: 2.2rem*0.6 1.3rem*0.6;
                right: 2rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 2.2rem * 0.8;
                height: 1.3rem * 0.8;
                background-size: 2.2rem*0.8 1.3rem*0.8;
                right: 2rem;
            }
        }
        select {
            background-color: transparent;
            border-radius: 3rem;
            border: solid 0.2rem var(--color-yellow-100);
            -webkit-appearance: none;
            -moz-appearance: none;
            box-sizing: border-box;
            width: 100%;
            height: 9.6rem;
            font-size: 2rem;
            padding: 0 4.4rem;
            color: var(--color-yellow-100);
            option {
                color: #333;
            }
            &:focus {
                border-color: var(--color-orange-500);
            }
            @include susy-breakpoint($s-320, $c8) {
                padding-left: 1.2rem;
                padding-right: 1.2rem;
                height: 4.8rem;
                border-radius: 1.2rem;
                font-size: 1.2rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                padding-left: 1.6rem;
                padding-right: 1.6rem;
                height: 5.6rem;
                border-radius: 1.6rem;
                font-size: 1.4rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                padding-left: 1.6rem;
                padding-right: 1.6rem;
                height: 6.4rem;
                border-radius: 2rem;
                font-size: 1.4rem;
            }
            @include susy-breakpoint($s-1201, $c18) {
                padding-left: 2.4rem;
                padding-right: 2.4rem;
                height: 8rem;
                border-radius: 2.4rem;
                font-size: 1.6rem;
            }
        }
    }
    &__btn-sample {
        display: flex;
        align-items: center;
        position: absolute;
        right: 0;
        top: 0;
        transform: translate3d(0,20%,0);
        border: 0;
        background-color: transparent;
        padding: 0;
        gap: 0.8rem;
        @include susy-breakpoint($s-320, $c8) {
            gap: 0.4rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            gap: 0.4rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            gap: 0.4rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            gap: 0.4rem;
        }
        span {
            line-height: 1;
            font-size: 2rem;
            color: var(--color-yellow-100);
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.2rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                font-size: 1.4rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 1.4rem;
            }
            @include susy-breakpoint($s-1201, $c18) {
                font-size: 1.6rem;
            }
        }
        img {
            display: block;
            margin: 0;
            width: 1.8rem;
            @include susy-breakpoint($s-320, $c8) {
                width: 1.2rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 1.4rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 1.4rem;
            }
        }
    }
    &__preview {
        height: 32rem;
        width: 100%;
        border-radius: 3rem;
        border: solid 0.2rem var(--color-yellow-100);
        margin-bottom: 1.6rem;
        overflow: hidden;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        img {
            width: 100%;
            object-fit: fill;
            display: block;
            margin: 0;
        }
        @include susy-breakpoint($s-320, $c8) {
            border-radius: 1.2rem;
            height: 20rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            border-radius: 1.6rem;
            height: 32rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            border-radius: 2rem;
            height: 32rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            border-radius: 2.4rem;
        }
    }
    &__btn-upload {
        cursor: pointer;
        border-radius: 3rem;
        height: 9.6rem;
        display: flex;
        justify-content: center;
        align-items: center;
        background: rgb(0,0,138);
        background: linear-gradient(0deg, rgba(0,0,138,1) 0%, rgba(24,62,217,1) 74%);
        border: solid 0.2rem var(--color-yellow-200-g);
        position: relative;
        &::after {
            content: "";
            background: transparent url('../images/icon-arrow-down@3x.png') no-repeat 0 0;
            background-size: 2.2rem 1.3rem;
            content: "";
            position: absolute;
            width: 2.2rem;
            height: 1.3rem;
            top: 50%;
            right: 2.4rem;
            transform: translate3d(0,-50%,0) rotate(180deg);
            @include susy-breakpoint($s-320, $c8) {
                width: 2.2rem*0.6;
                height: 1.3rem*0.6;
                background-size: 2.2rem*0.6 1.3rem*0.6;
                right: 1.6rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 2.2rem*0.6;
                height: 1.3rem*0.6;
                background-size: 2.2rem*0.6 1.3rem*0.6;
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 2.2rem*0.8;
                height: 1.3rem*0.8;
                background-size: 2.2rem*0.8 1.3rem*0.8;
            }
        }
        span {
            font-size: 2rem;
            background: rgb(231,136,0);
            background: linear-gradient(1deg, rgba(231,136,0,1) 20%, rgba(247,232,174,1) 68%, rgba(218,156,69,1) 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            font-weight: 500;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.4rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                font-size: 1.6rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 1.6rem;
            }
        }
        @include susy-breakpoint($s-320, $c8) {
            border-radius: 1.2rem;
            height: 4.8rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            border-radius: 1.6rem;
            height: 5.6rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            border-radius: 2rem;
            height: 6.4rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            border-radius: 2.4rem;
            height: 8rem;
        }
    }
    &__checkbox-wrap,
    &__radio-wrap {
        display: flex;
        gap: 3.2rem;
        justify-content: flex-start;
        align-items: center;
        padding: 0 4.4rem;
        @include susy-breakpoint($s-320, $c8) {
            padding-left: 1.2rem;
            padding-right: 1.2rem;
            gap: 1.2rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            padding-left: 1.6rem;
            padding-right: 1.6rem;
            gap: 1.6rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            padding-left: 2rem;
            padding-right: 2rem;
            gap: 1.6rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            padding-left: 2.4rem;
            padding-right: 2.4rem;
            gap: 2.4rem;
        }
    }
    &__checkbox-wrap {
        @include susy-breakpoint($s-320, $c8) {
            display: grid;
            grid-template-columns: repeat(2, minmax(0, 1fr));
            gap: 0.8rem;
        }
        &:not(:last-child) {
            margin-bottom: 1.6rem;
            @include susy-breakpoint($s-320, $c8) {
                margin-bottom: 0.4rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                margin-bottom: 1.2rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                margin-bottom: 1.2rem;
            }
        }
        #{$self}__input {
            flex: 1;
            @include susy-breakpoint($s-320, $c8) {
                grid-column: 2 / 4;
            }
        }
    }
    &__checkbox {
        input[type=checkbox] {
            position: absolute;
            left: -9999px;
            box-sizing: border-box;
            padding: 0;
        }
        input[type=checkbox]:not(:checked)+label,
        input[type=checkbox]:checked+label {
            position: relative;
            padding-left: 5.6rem;
            cursor: pointer;
            height: 4.8rem;
            line-height: 4.8rem;
            display: inline-block;
            color: var(--color-yellow-100);
            font-size: 2rem;
            @include susy-breakpoint($s-320, $c8) {
                height: 2.4rem;
                line-height: 2.4rem;
                font-size: 1.2rem;
                padding-left: 2.8rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                height: 2.8rem;
                line-height: 2.8rem;
                font-size: 1.4rem;
                padding-left: 3.2rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                height: 3.6rem;
                line-height: 3.6rem;
                font-size: 1.4rem;
                padding-left: 4rem;
            }
            @include susy-breakpoint($s-1201, $c18) {
                font-size: 1.6rem;
            }
        }
        input[type=checkbox]:not(:checked)+label:before,
        input[type=checkbox]:checked+label:before {
            content: '';
            position: absolute;
            left: 0;
            border-radius: 0.6rem;
            width: 4.8rem;
            height: 4.8rem;
            border: solid 0.2rem var(--color-yellow-100);
            background-color: transparent;
            top: 0;
            box-sizing: border-box;
            @include susy-breakpoint($s-320, $c8) {
                width: 2.4rem;
                height: 2.4rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 2.8rem;
                height: 2.8rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 3.6rem;
                height: 3.6rem;
            }
        }
        input[type=checkbox]+label:after {
            content: "";
            width: 1.8rem;
            height: 1.4rem;
            background: transparent url(../images/icon-agree@3x.png) no-repeat center center;
            background-size: 1.8rem 1.4rem;
            position: absolute;
            top: 50%;
            left: 0;
            transform: translate3d(80%, -60%, 0);
            border: solid 0 transparent;
            transition: all 300ms ease;
            opacity: 0;
            @include susy-breakpoint($s-320, $c8) {
                width: 1.8rem*0.6;
                height: 1.4rem*0.6;
                background-size: 1.8rem*0.6 1.4rem*0.6;
                transform: translate3d(60%, -60%, 0);
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 1.8rem*0.6;
                height: 1.4rem*0.6;
                background-size: 1.8rem*0.6 1.4rem*0.6;
                transform: translate3d(80%, -60%, 0);
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 1.8rem*0.8;
                height: 1.4rem*0.8;
                background-size: 1.8rem*0.8 1.4rem*0.8;
                transform: translate3d(74%, -60%, 0);
            }
        }
        input[type=checkbox]:checked+label:after {
            opacity: 1;
        }
    }
    &__radio {
        input[type=radio] {
            position: absolute;
            left: -9999px;
            box-sizing: border-box;
            padding: 0;
        }
        input[type=radio]:not(:checked)+label,
        input[type=radio]:checked+label {
            position: relative;
            padding-left: 5.6rem;
            cursor: pointer;
            height: 4.8rem;
            line-height: 4.8rem;
            display: inline-block;
            color: var(--color-yellow-100);
            font-size: 2rem;
            @include susy-breakpoint($s-320, $c8) {
                height: 2.4rem;
                line-height: 2.4rem;
                font-size: 1.2rem;
                padding-left: 2.8rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                height: 2.8rem;
                line-height: 2.8rem;
                font-size: 1.4rem;
                padding-left: 3.2rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                height: 3.6rem;
                line-height: 3.6rem;
                font-size: 1.4rem;
                padding-left: 4rem;
            }
            @include susy-breakpoint($s-1201, $c18) {
                font-size: 1.6rem;
            }
        }
        input[type=radio]:not(:checked)+label:before,
        input[type=radio]:checked+label:before {
            content: '';
            position: absolute;
            left: 0;
            border-radius: 0.6rem;
            width: 4.8rem;
            height: 4.8rem;
            border: solid 0.2rem var(--color-yellow-100);
            background-color: transparent;
            top: 0;
            box-sizing: border-box;
            @include susy-breakpoint($s-320, $c8) {
                width: 2.4rem;
                height: 2.4rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 2.8rem;
                height: 2.8rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 3.6rem;
                height: 3.6rem;
            }
        }
        input[type=radio]+label:after {
            content: "";
            width: 1.8rem;
            height: 1.4rem;
            background: transparent url(../images/icon-agree@3x.png) no-repeat center center;
            background-size: 1.8rem 1.4rem;
            position: absolute;
            top: 50%;
            left: 0;
            transform: translate3d(80%, -60%, 0);
            border: solid 0 transparent;
            transition: all 300ms ease;
            opacity: 0;
            @include susy-breakpoint($s-320, $c8) {
                width: 1.8rem*0.6;
                height: 1.4rem*0.6;
                background-size: 1.8rem*0.6 1.4rem*0.6;
                transform: translate3d(60%, -60%, 0);
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 1.8rem*0.6;
                height: 1.4rem*0.6;
                background-size: 1.8rem*0.6 1.4rem*0.6;
                transform: translate3d(80%, -60%, 0);
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 1.8rem*0.8;
                height: 1.4rem*0.8;
                background-size: 1.8rem*0.8 1.4rem*0.8;
                transform: translate3d(74%, -60%, 0);
            }
        }
        input[type=radio]:checked+label:after {
            opacity: 1;
        }
    }
    &__agreement {
        padding: 6.6rem 0;
        @include susy-breakpoint($s-320, $c8) {
            padding-top: 3.2rem;
            padding-bottom: 3.2rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            padding-top: 4rem;
            padding-bottom: 4rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            padding-top: 4.8rem;
            padding-bottom: 4.8rem;
        }
    }
    &__agreement-checkbox {
        text-align: center;
        input[type=checkbox] {
            position: absolute;
            left: -9999px;
            box-sizing: border-box;
            padding: 0;
        }
        input[type=checkbox]:not(:checked)+label,
        input[type=checkbox]:checked+label {
            position: relative;
            padding-left: 3.6rem;
            cursor: pointer;
            height: 3rem;
            line-height: 3rem;
            display: inline-block;
            color: var(--color-yellow-100);
            font-size: 2rem;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.2rem;
                height: 2rem;
                line-height: 2rem;
                padding-left: 2.4rem;
                text-align: left;
            }
            @include susy-breakpoint($s-768, $c8) {
                font-size: 1.4rem;
                height: 2rem;
                line-height: 2rem;
                padding-left: 2.4rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 1.4rem;
                height: 2.4rem;
                line-height: 2.4rem;
                padding-left: 2.8rem;
            }
            @include susy-breakpoint($s-1201, $c18) {
                font-size: 1.6rem;
            }
        }
        input[type=checkbox]:not(:checked)+label:before,
        input[type=checkbox]:checked+label:before {
            content: '';
            position: absolute;
            left: 0;
            border-radius: 0;
            width: 3rem;
            height: 3rem;
            border: solid 0.2rem var(--color-yellow-100);
            background-color: transparent;
            top: 0;
            box-sizing: border-box;
            @include susy-breakpoint($s-320, $c8) {
                width: 2rem;
                height: 2rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 2rem;
                height: 2rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 2.4rem;
                height: 2.4rem;
            }
        }
        input[type=checkbox]+label:after {
            content: "";
            width: 1.8rem;
            height: 1.4rem;
            background: transparent url(../images/icon-agree@3x.png) no-repeat center center;
            background-size: 1.8rem 1.4rem;
            position: absolute;
            top: 50%;
            left: 0;
            transform: translate3d(36%, -60%, 0);
            border: solid 0 transparent;
            transition: all 300ms ease;
            opacity: 0;
            @include susy-breakpoint($s-320, $c8) {
                width: 1.8rem*0.6;
                height: 1.4rem*0.6;
                background-size: 1.8rem*0.6 1.4rem*0.6;
                transform: translate3d(45%, -60%, 0);
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 1.8rem*0.6;
                height: 1.4rem*0.6;
                background-size: 1.8rem*0.6 1.4rem*0.6;
                transform: translate3d(40%, -60%, 0);
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 1.8rem*0.8;
                height: 1.4rem*0.8;
                background-size: 1.8rem*0.8 1.4rem*0.8;
            }
        }
        input[type=checkbox]:checked+label:after {
            opacity: 1;
        }
    }
    .g-recaptcha {
        display: flex;
        justify-content: center;
    }
    &__btn-submit {
        width: 100%;
        cursor: pointer;
        border-radius: 3rem;
        height: 9.6rem;
        display: flex;
        justify-content: center;
        align-items: center;
        background: rgb(0,0,138);
        background: linear-gradient(0deg, rgba(0,0,138,1) 0%, rgba(24,62,217,1) 74%);
        border: solid 0.2rem var(--color-yellow-200-g);
        position: relative;
        &::after {
            content: "";
            background: transparent url('../images/icon-arrow-down@3x.png') no-repeat 0 0;
            background-size: 2.2rem 1.3rem;
            content: "";
            position: absolute;
            width: 2.2rem;
            height: 1.3rem;
            top: 50%;
            right: 2.4rem;
            transform: translate3d(0,-50%,0) rotate(-90deg);
            @include susy-breakpoint($s-320, $c8) {
                width: 2.2rem * 0.6;
                height: 1.3rem * 0.6;
                background-size: 2.2rem*0.6 1.3rem*0.6;
                right: 1.6rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 2.2rem * 0.6;
                height: 1.3rem * 0.6;
                background-size: 2.2rem*0.6 1.3rem*0.6;
                right: 2rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 2.2rem * 0.8;
                height: 1.3rem * 0.8;
                background-size: 2.2rem*0.8 1.3rem*0.8;
                right: 2rem;
            }
        }
        span {
            font-size: 2rem;
            background: rgb(231,136,0);
            background: linear-gradient(1deg, rgba(231,136,0,1) 20%, rgba(247,232,174,1) 68%, rgba(218,156,69,1) 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            font-weight: 500;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.6rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                font-size: 1.6rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 1.6rem;
            }
        }
        @include susy-breakpoint($s-320, $c8) {
            height: 5.6rem;
            border-radius: 1.2rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            height: 6.4rem;
            border-radius: 1.6rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            height: 8rem;
            border-radius: 2rem;
        }
        @include susy-breakpoint($s-1601, $c18) {
            border-radius: 2rem;
        }
    }
}