.site-footer {
    background-color: var(--color-main-800);
    &__top {
        padding: 2.4rem 0;
        display: flex;
        align-items: center;
        justify-content: center;
        @include susy-breakpoint($s-320, $c8) {
            padding-top: 3.2rem;
            padding-bottom: 3.2rem;
        }
    }
    &__logo {
        width: 6rem;
        @include susy-breakpoint($s-320, $c8) {
            width: 3.6rem;
        }
    }
    &__bottom {
        padding-bottom: 2.4rem;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &__copyright {
        font-size: 1rem;
        color: #b2b2b2;
        line-height: 1.6;
        @include susy-breakpoint($s-320, $c8) {
            font-size: 0.8rem;
            text-align: center;
        }
    }
}