.section-success {
    width: 100%;
    background: #001892 url('../images/success-bg.jpg') no-repeat center top;
    background-size: cover;
    min-height: 80rem;
    @include susy-breakpoint($s-320, $c8) {
        background-image: url('../images/success-bg-m.jpg');
        background-size: 100% auto;
        min-height: 120vw;
    }
    @include susy-breakpoint($s-768, $c8) {
        min-height: 42rem;
    }
    @include susy-breakpoint($s-960, $c12) {
        min-height: 48rem;
    }
    @include susy-breakpoint($s-1201, $c18) {
        min-height: 60rem;
    }
    @include susy-breakpoint($s-1601, $c18) {
        min-height: 72rem;
    }
    &__inner {
        margin: 0 auto;
        width: 92rem;
        padding-top: 22%;
        @include susy-breakpoint($s-320, $c8) {
            width: 100%;
            padding-top: 80vw;
        }
        @include susy-breakpoint($s-768, $c8) {
            width: auto;
        }
        @include susy-breakpoint($s-960, $c12) {
            width: 80rem;
        }
    }
    &__title {
        font-size: 6rem;
        font-weight: 900;
        background: rgb(244,227,172);
        background: radial-gradient(circle, rgba(244,227,172,1) 0%, rgba(224,180,111,1) 100%);
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
        margin-bottom: 2rem;
        text-align: center;
        line-height: 1.6;
        br {
            display: none;
        }
        @include susy-breakpoint($s-320, $c8) {
            font-size: 2.4rem;
            margin-bottom: 1.6rem;
            br {
                display: block;
            }
        }
        @include susy-breakpoint($s-768, $c8) {
            font-size: 3.2rem;
            margin-bottom: 2rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            font-size: 4.8rem;
            margin-bottom: 2.4rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            font-size: 5.6rem;
            margin-bottom: 3rem;
        }
    }
    &__btn-signup {
        width: 46rem;
        margin: 0 auto;
        border-radius: 4rem;
        height: 9.6rem;
        display: flex;
        justify-content: center;
        align-items: center;
        background: rgb(0,0,138);
        background: linear-gradient(0deg, rgba(0,0,138,1) 0%, rgba(24,62,217,1) 74%);
        border: solid 0.2rem var(--color-yellow-200-g);
        position: relative;
        &::after {
            content: "";
            background: transparent url('../images/icon-arrow-down@3x.png') no-repeat 0 0;
            background-size: 2.2rem 1.3rem;
            content: "";
            position: absolute;
            width: 2.2rem;
            height: 1.3rem;
            top: 50%;
            right: 2.4rem;
            transform: translate3d(0,-50%,0) rotate(270deg);
            @include susy-breakpoint($s-320, $c8) {
                width: 2.2rem*0.6;
                height: 1.3rem*0.6;
                background-size: 2.2rem*0.6 1.3rem*0.6;
                right: 1.6rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                width: 2.2rem*0.6;
                height: 1.3rem*0.6;
                background-size: 2.2rem*0.6 1.3rem*0.6;
            }
            @include susy-breakpoint($s-960, $c12) {
                width: 2.2rem*0.8;
                height: 1.3rem*0.8;
                background-size: 2.2rem*0.8 1.3rem*0.8;
            }
        }
        span {
            font-size: 3rem;
            background: rgb(244,227,172);
            background: radial-gradient(circle, rgba(244,227,172,1) 0%, rgba(224,180,111,1) 100%);
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
            font-weight: 900;
            @include susy-breakpoint($s-320, $c8) {
                font-size: 1.8rem;
            }
            @include susy-breakpoint($s-768, $c8) {
                font-size: 2.4rem;
            }
            @include susy-breakpoint($s-960, $c12) {
                font-size: 2.8rem;
            }
        }
        @include susy-breakpoint($s-320, $c8) {
            border-radius: 2.4rem;
            height: 6rem;
            width: 28rem;
        }
        @include susy-breakpoint($s-768, $c8) {
            border-radius: 3.2rem;
            height: 8rem;
        }
        @include susy-breakpoint($s-960, $c12) {
            border-radius: 3.6rem;
            height: 7.2rem;
        }
        @include susy-breakpoint($s-1201, $c18) {
            border-radius: 4rem;
            height: 8.8rem;
        }
    }
}